'use client'

import { useState, useRef, DragEvent, useEffect } from 'react'
import { Image as ImageIcon, Loader2, Plus, Edit3, Trash2, ChevronUp, ChevronDown, GripVertical } from 'lucide-react'
import React from 'react'

interface UploadedImage {
  id: string
  url: string
  file?: File
  isEditing?: boolean
}

interface MultipleImageUploadProps {
  onImagesChange: (images: string[]) => void
  currentImages?: string[]
  maxImages?: number
}

export function MultipleImageUpload({ onImagesChange, currentImages = [], maxImages = 5 }: MultipleImageUploadProps) {
  const [images, setImages] = useState<UploadedImage[]>([])
  const [isUploading, setIsUploading] = useState(false)
  const [isDragOver, setIsDragOver] = useState(false)
  const [editingImage, setEditingImage] = useState<File | null>(null)
  const [editingImageId, setEditingImageId] = useState<string | null>(null)
  const [draggedImageId, setDraggedImageId] = useState<string | null>(null)
  const [dragOverImageId, setDragOverImageId] = useState<string | null>(null)
  const [isInitialized, setIsInitialized] = useState(false)
  const fileInputRef = useRef<HTMLInputElement>(null)

  // Ï¥àÍ∏∞ Ïù¥ÎØ∏ÏßÄ ÏÑ§Ï†ïÏùÑ useEffectÎ°ú Ïù¥Îèô
  useEffect(() => {
    if (currentImages.length > 0 && !isInitialized) {
      const initialImages = currentImages.map((url, index) => ({ 
        id: `existing-${index}`, 
        url 
      }))
      setImages(initialImages)
      setIsInitialized(true)
    } else if (currentImages.length === 0 && !isInitialized) {
      // Ï¥àÍ∏∞ Ïù¥ÎØ∏ÏßÄÍ∞Ä ÏóÜÏñ¥ÎèÑ Ï¥àÍ∏∞Ìôî ÏôÑÎ£åÎ°ú ÌëúÏãú
      setIsInitialized(true)
    }
  }, [currentImages, isInitialized])

  // Ïù¥ÎØ∏ÏßÄ ÏÉÅÌÉúÍ∞Ä Î≥ÄÍ≤ΩÎê† ÎïåÎßàÎã§ Î∂ÄÎ™®ÏóêÍ≤å ÏïåÎ¶º (useEffect ÏÇ¨Ïö©ÏúºÎ°ú Î†åÎçîÎßÅ ÏóêÎü¨ Î∞©ÏßÄ)
  useEffect(() => {
    if (isInitialized) {
      const imageUrls = images.map(img => img.url)
      console.log('=== useEffect Ïù¥ÎØ∏ÏßÄ Î≥ÄÍ≤Ω ÏïåÎ¶º ===')
      console.log('ÌòÑÏû¨ images:', images)
      console.log('Ï∂îÏ∂úÎêú URLs:', imageUrls)
      onImagesChange(imageUrls)
    }
  }, [images, isInitialized, onImagesChange])

  const handleFileUpload = async (files: FileList | File[]) => {
    if (images.length >= maxImages) {
      alert(`ÏµúÎåÄ ${maxImages}Í∞úÏùò Ïù¥ÎØ∏ÏßÄÎßå ÏóÖÎ°úÎìúÌï† Ïàò ÏûàÏäµÎãàÎã§.`)
      return
    }

    setIsUploading(true)
    
    try {
      const fileArray = Array.from(files)
      const availableSlots = maxImages - images.length
      const filesToUpload = fileArray.slice(0, availableSlots)

      for (const file of filesToUpload) {
        const formData = new FormData()
        formData.append('image', file)
        formData.append('translateImage', 'false') // Í∏∞Î≥∏Ï†ÅÏúºÎ°ú ÏõêÎ≥∏ Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú

        const response = await fetch('/api/upload', {
          method: 'POST',
          body: formData,
        })

        const result = await response.json()
        
        if (result.success) {
          const newImage: UploadedImage = {
            id: `img-${Date.now()}-${Math.random()}`,
            url: result.imageUrl,
            file: file
          }
          
          setImages(prev => {
            const updated = [...prev, newImage]
            // ÏßÅÏ†ë ÏïåÎ¶º Ï†úÍ±∞ - useEffectÍ∞Ä Ï≤òÎ¶¨Ìï®
            return updated
          })
        } else {
          throw new Error(result.error || 'Upload failed')
        }
      }
    } catch (error) {
      console.error('Upload error:', error)
      alert('Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìúÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.')
    } finally {
      setIsUploading(false)
    }
  }

  const handleRemoveImage = (imageId: string) => {
    setImages(prev => {
      const updated = prev.filter(img => img.id !== imageId)
      // ÏßÅÏ†ë ÏïåÎ¶º Ï†úÍ±∞ - useEffectÍ∞Ä Ï≤òÎ¶¨Ìï®
      return updated
    })
  }

  const handleEditImage = (imageId: string) => {
    const image = images.find(img => img.id === imageId)
    if (image && image.file) {
      setEditingImage(image.file)
      setEditingImageId(imageId)
    } else {
      alert('Ïù¥ Ïù¥ÎØ∏ÏßÄÎäî Ìé∏ÏßëÌï† Ïàò ÏóÜÏäµÎãàÎã§. (ÏõêÎ≥∏ ÌååÏùºÏù¥ ÏóÜÏùå)')
    }
  }

  // ÏàúÏÑú Î≥ÄÍ≤Ω Ìï®Ïàò
  const moveImage = (fromIndex: number, toIndex: number) => {
    setImages(prev => {
      const updated = [...prev]
      const [movedImage] = updated.splice(fromIndex, 1)
      updated.splice(toIndex, 0, movedImage)
      // ÏßÅÏ†ë ÏïåÎ¶º Ï†úÍ±∞ - useEffectÍ∞Ä Ï≤òÎ¶¨Ìï®
      return updated
    })
  }

  // ÌôîÏÇ¥Ìëú Î≤ÑÌäºÏúºÎ°ú ÏàúÏÑú Î≥ÄÍ≤Ω
  const handleMoveUp = (imageId: string) => {
    const currentIndex = images.findIndex(img => img.id === imageId)
    if (currentIndex > 0) {
      moveImage(currentIndex, currentIndex - 1)
    }
  }

  const handleMoveDown = (imageId: string) => {
    const currentIndex = images.findIndex(img => img.id === imageId)
    if (currentIndex < images.length - 1) {
      moveImage(currentIndex, currentIndex + 1)
    }
  }

  // ÎìúÎûòÍ∑∏ Ïï§ ÎìúÎ°≠ Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨Îì§
  const handleImageDragStart = (e: React.DragEvent, imageId: string) => {
    setDraggedImageId(imageId)
    e.dataTransfer.effectAllowed = 'move'
  }

  const handleImageDragOver = (e: React.DragEvent, imageId: string) => {
    e.preventDefault()
    e.dataTransfer.dropEffect = 'move'
    setDragOverImageId(imageId)
  }

  const handleImageDragLeave = () => {
    setDragOverImageId(null)
  }

  const handleImageDrop = (e: React.DragEvent, targetImageId: string) => {
    e.preventDefault()
    
    if (draggedImageId && draggedImageId !== targetImageId) {
      const fromIndex = images.findIndex(img => img.id === draggedImageId)
      const toIndex = images.findIndex(img => img.id === targetImageId)
      
      if (fromIndex !== -1 && toIndex !== -1) {
        moveImage(fromIndex, toIndex)
      }
    }
    
    setDraggedImageId(null)
    setDragOverImageId(null)
  }

  const handleEditComplete = (translatedImageUrl: string) => {
    if (editingImageId) {
      setImages(prev => {
        const updated = prev.map(img => 
          img.id === editingImageId 
            ? { ...img, url: translatedImageUrl }
            : img
        )
        // ÏßÅÏ†ë ÏïåÎ¶º Ï†úÍ±∞ - useEffectÍ∞Ä Ï≤òÎ¶¨Ìï®
        return updated
      })
    }
    
    setEditingImage(null)
    setEditingImageId(null)
  }

  const handleEditCancel = () => {
    setEditingImage(null)
    setEditingImageId(null)
  }

  const handleDrop = (e: DragEvent<HTMLDivElement>) => {
    e.preventDefault()
    setIsDragOver(false)
    
    const files = e.dataTransfer.files
    if (files.length > 0) {
      handleFileUpload(files)
    }
  }

  const handleDragOver = (e: DragEvent<HTMLDivElement>) => {
    e.preventDefault()
    setIsDragOver(true)
  }

  const handleDragLeave = (e: DragEvent<HTMLDivElement>) => {
    e.preventDefault()
    setIsDragOver(false)
  }

  const handleFileSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files
    if (files && files.length > 0) {
      handleFileUpload(files)
    }
  }

  const handleClick = () => {
    if (images.length < maxImages) {
      fileInputRef.current?.click()
    }
  }

  // Ìé∏Ïßë Î™®ÎìúÏù∏ Í≤ΩÏö∞
  if (editingImage) {
    const ImageTranslator = React.lazy(() => import('./ImageTranslator'))
    
    return (
      <div className="w-full">
        <div className="mb-4 p-4 bg-blue-50 rounded-lg border border-blue-200">
          <h3 className="text-lg font-medium text-blue-900 mb-2">Ïù¥ÎØ∏ÏßÄ Î≤àÏó≠ Ìé∏Ïßë</h3>
          <p className="text-sm text-blue-700">ÏÑ†ÌÉùÎêú Ïù¥ÎØ∏ÏßÄÎ•º Ìé∏ÏßëÌïòÍ≥† ÏûàÏäµÎãàÎã§. ÏôÑÎ£å ÌõÑ Ïù¥ÎØ∏ÏßÄ Î™©Î°ùÏúºÎ°ú ÎèåÏïÑÍ∞ëÎãàÎã§.</p>
        </div>
        
        <React.Suspense fallback={<div className="text-center py-8">ÏóêÎîîÌÑ∞ Î°úÎî© Ï§ë...</div>}>
          <ImageTranslator
            imageFile={editingImage}
            onTranslationComplete={handleEditComplete}
            onCancel={handleEditCancel}
          />
        </React.Suspense>
      </div>
    )
  }

  return (
    <div className="w-full space-y-4">
      <input
        ref={fileInputRef}
        type="file"
        accept="image/*"
        multiple
        onChange={handleFileSelect}
        className="hidden"
      />

      {/* ÏóÖÎ°úÎìúÎêú Ïù¥ÎØ∏ÏßÄÎì§ */}
      {images.length > 0 && (
        <div className="space-y-4">
          <div className="flex items-center justify-between">
            <h4 className="text-sm font-medium text-gray-700">ÏóÖÎ°úÎìúÎêú Ïù¥ÎØ∏ÏßÄ ({images.length}Í∞ú)</h4>
            <p className="text-xs text-gray-500">ÎìúÎûòÍ∑∏ÌïòÍ±∞ÎÇò ÌôîÏÇ¥ÌëúÎ°ú ÏàúÏÑú Î≥ÄÍ≤Ω</p>
          </div>
          
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {images.map((image, index) => (
              <div 
                key={image.id} 
                className={`relative group border-2 rounded-lg transition-all ${
                  draggedImageId === image.id ? 'opacity-50' : 'opacity-100'
                } ${
                  dragOverImageId === image.id ? 'border-blue-500 bg-blue-50' : 'border-gray-200'
                }`}
                draggable
                onDragStart={(e) => handleImageDragStart(e, image.id)}
                onDragOver={(e) => handleImageDragOver(e, image.id)}
                onDragLeave={handleImageDragLeave}
                onDrop={(e) => handleImageDrop(e, image.id)}
              >
                {/* ÏàúÏÑú Î≤àÌò∏ */}
                <div className="absolute top-2 left-2 bg-black bg-opacity-70 text-white text-xs px-2 py-1 rounded-full z-10">
                  {index + 1}
                </div>
                
                {/* ÎìúÎûòÍ∑∏ Ìï∏Îì§ */}
                <div className="absolute top-2 right-2 bg-black bg-opacity-70 text-white p-1 rounded cursor-move z-10">
                  <GripVertical className="h-4 w-4" />
                </div>
                
                <div className="aspect-video rounded-lg overflow-hidden">
                  <img 
                    src={image.url} 
                    alt={`Ïù¥ÎØ∏ÏßÄ ${index + 1}`} 
                    className="w-full h-full object-cover"
                  />
                </div>
                
                {/* Ïù¥ÎØ∏ÏßÄ Ïï°ÏÖò Î≤ÑÌäºÎì§ */}
                <div className="absolute inset-0 bg-black bg-opacity-40 opacity-0 group-hover:opacity-100 transition-opacity rounded-lg flex items-center justify-center">
                  <div className="flex flex-col gap-2">
                    {/* ÏàúÏÑú Î≥ÄÍ≤Ω Î≤ÑÌäºÎì§ */}
                    <div className="flex gap-2">
                      <button
                        type="button"
                        onClick={() => handleMoveUp(image.id)}
                        disabled={index === 0}
                        className="p-2 bg-gray-600 text-white rounded-full hover:bg-gray-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                        title="ÏúÑÎ°ú Ïù¥Îèô"
                      >
                        <ChevronUp className="h-4 w-4" />
                      </button>
                      <button
                        type="button"
                        onClick={() => handleMoveDown(image.id)}
                        disabled={index === images.length - 1}
                        className="p-2 bg-gray-600 text-white rounded-full hover:bg-gray-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                        title="ÏïÑÎûòÎ°ú Ïù¥Îèô"
                      >
                        <ChevronDown className="h-4 w-4" />
                      </button>
                    </div>
                    
                    {/* Ìé∏Ïßë/ÏÇ≠Ï†ú Î≤ÑÌäºÎì§ */}
                    <div className="flex gap-2">
                      {image.file && (
                        <button
                          type="button"
                          onClick={() => handleEditImage(image.id)}
                          className="p-2 bg-blue-600 text-white rounded-full hover:bg-blue-700 transition-colors"
                          title="Ïù¥ÎØ∏ÏßÄ Ìé∏Ïßë"
                        >
                          <Edit3 className="h-4 w-4" />
                        </button>
                      )}
                      <button
                        type="button"
                        onClick={() => handleRemoveImage(image.id)}
                        className="p-2 bg-red-600 text-white rounded-full hover:bg-red-700 transition-colors"
                        title="Ïù¥ÎØ∏ÏßÄ ÏÇ≠Ï†ú"
                      >
                        <Trash2 className="h-4 w-4" />
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* ÏÉà Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú ÏòÅÏó≠ */}
      {images.length < maxImages && (
        <div
          onDrop={handleDrop}
          onDragOver={handleDragOver}
          onDragLeave={handleDragLeave}
          onClick={handleClick}
          className={`relative border-2 border-dashed rounded-lg p-8 text-center cursor-pointer transition-colors ${
            isDragOver
              ? 'border-blue-500 bg-blue-50'
              : 'border-gray-300 hover:border-gray-400'
          } ${isUploading ? 'pointer-events-none opacity-50' : ''}`}
        >
          {isUploading ? (
            <div className="flex flex-col items-center gap-3">
              <Loader2 className="h-8 w-8 animate-spin text-blue-600" />
              <p className="text-gray-600">Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ï§ë...</p>
            </div>
          ) : (
            <div className="flex flex-col items-center gap-3">
              <div className="p-3 bg-gray-100 rounded-full">
                {images.length === 0 ? (
                  <ImageIcon className="h-8 w-8 text-gray-600" />
                ) : (
                  <Plus className="h-8 w-8 text-gray-600" />
                )}
              </div>
              <div>
                <p className="text-lg font-medium text-gray-900">
                  {images.length === 0 ? 'Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú' : 'Ïù¥ÎØ∏ÏßÄ Ï∂îÍ∞Ä'}
                </p>
                <p className="text-sm text-gray-600 mt-1">
                  Ïù¥ÎØ∏ÏßÄÎ•º ÎìúÎûòÍ∑∏ÌïòÍ±∞ÎÇò ÌÅ¥Î¶≠ÌïòÏó¨ ÏóÖÎ°úÎìú ({images.length}/{maxImages})
                </p>
                <p className="text-xs text-gray-500 mt-2">
                  PNG, JPG, GIF ÏßÄÏõê ‚Ä¢ Ïó¨Îü¨ Ïù¥ÎØ∏ÏßÄ ÎèôÏãú ÏóÖÎ°úÎìú Í∞ÄÎä•
                </p>
              </div>
            </div>
          )}
        </div>
      )}

      {/* ÎèÑÏõÄÎßê */}
      <div className="bg-gray-50 rounded-lg p-4">
        <h4 className="text-sm font-medium text-gray-900 mb-2">üí° ÏÇ¨Ïö©Î≤ï</h4>
        <ul className="text-xs text-gray-600 space-y-1">
          <li>‚Ä¢ Ïó¨Îü¨ Ïù¥ÎØ∏ÏßÄÎ•º Ìïú Î≤àÏóê ÏóÖÎ°úÎìúÌï† Ïàò ÏûàÏäµÎãàÎã§</li>
          <li>‚Ä¢ Ïù¥ÎØ∏ÏßÄÎ•º ÎìúÎûòÍ∑∏ÌïòÏó¨ ÏàúÏÑúÎ•º Î≥ÄÍ≤ΩÌï† Ïàò ÏûàÏäµÎãàÎã§</li>
          <li>‚Ä¢ Ïù¥ÎØ∏ÏßÄ ÏúÑÏóê ÎßàÏö∞Ïä§Î•º Ïò¨Î¶¨Î©¥ Ìé∏Ïßë/ÏÇ≠Ï†ú/ÏàúÏÑúÎ≥ÄÍ≤Ω Î≤ÑÌäºÏù¥ ÎÇòÌÉÄÎÇ©ÎãàÎã§</li>
          <li>‚Ä¢ Ìé∏Ïßë Î≤ÑÌäºÏùÑ ÌÅ¥Î¶≠ÌïòÎ©¥ ÌïúÍµ≠Ïñ¥ ÌÖçÏä§Ìä∏Î•º Î≤àÏó≠Ìï† Ïàò ÏûàÏäµÎãàÎã§</li>
          <li>‚Ä¢ ÏµúÎåÄ {maxImages}Í∞úÏùò Ïù¥ÎØ∏ÏßÄÍπåÏßÄ ÏóÖÎ°úÎìú Í∞ÄÎä•Ìï©ÎãàÎã§</li>
        </ul>
      </div>
    </div>
  )
} 